import type { FailureDomain, ServiceToken, ServiceTokenType, TestDevice } from "core/types/api/testing"

export function getServiceTokens() {
  return api.get<ServiceToken[]>("get_service_tokens")
}

export function createServiceToken(data: { type?: ServiceTokenType; app_identifier: string }) {
  return api.post<ServiceToken>("create_service_token", { data })
}

export function deleteServiceToken(data: { token: string }) {
  return api.post("delete_service_token", { data })
}

export function getTestDevices(params: { udid?: string } = {}) {
  return api.get<TestDevice[]>("get_test_devices", { params })
}

export function editTestDevice(data: { udid: string; failure_domain_identifier: string }) {
  return api.post<TestDevice>("edit_test_device", { data })
}

export function deleteTestDevice(data: { udid: string }) {
  return api.post("delete_test_device", { data })
}

export function getFailureDomains() {
  return api.get<FailureDomain[]>("get_failure_domains")
}
